@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
*,*::before,*::after{box-sizing:border-box}body{margin:0;font-size:15px;background-color:#effafa}.tags{display:flex;flex-flow:row wrap;justify-content:flex-start;align-items:center;flex-shrink:3}@media (max-width: 500px){.tags{border-top:1px solid #7b8e8e;margin-top:1rem;padding-top:1rem}}.tag{text-decoration:none;color:#5ba4a4;background-color:#effafa;padding:0.5rem;margin:0.5rem 0.5rem 0.5rem 0;font-weight:700;border-radius:5px;transition:0.3s}.tag:hover{background-color:#5ba4a4;color:#fff;cursor:pointer}.card-wrapper{position:relative;width:90%;margin:2rem auto;margin-top:4rem}.card{display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center;width:100%;background-color:#fff;padding:2rem;border-radius:15px;overflow:hidden;box-shadow:0px 5px 5px #7b8e8e}@media (max-width: 500px){.card{flex-flow:column}}.card.featured-card::before{content:"";position:absolute;width:0.5rem;height:100%;background-color:#5ba4a4;left:0;top:0;border-top-left-radius:15px;border-bottom-left-radius:15px}.card .logo-container{border-radius:50%;width:20%;height:auto;flex-shrink:1}@media (max-width: 500px){.card .logo-container{position:absolute;top:-2rem;left:2rem}}.card .logo-container img{width:100%}.card .details{margin-right:auto;margin-left:2rem;width:35%;min-width:200px;flex-shrink:1}@media (max-width: 500px){.card .details{width:100%;margin:0;margin-top:1rem}}.card .details div h5{display:inline-block;margin:0;margin-right:0.5rem}.card .details .name{color:#5ba4a4;font-size:18px}.card .details .btn{padding:0.5rem 1rem;border-radius:2rem}.card .details .new{background-color:#5ba4a4;color:#fff}.card .details .featured{background-color:#2c3a3a;color:#fff}.card .details .position{font-size:27px;margin:1rem 0;font-family:"Spartan",sans-serif}.card .details .position:hover{color:#5ba4a4;cursor:pointer}.card .details .day-time-locate{color:#7b8e8e;font-size:22.5px;font-weight:lighter;display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center}#filter-bar{width:90%;display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center;background-color:#fff;margin:0 auto;margin-top:-2rem;border-radius:15px;padding:1rem 2rem;box-shadow:0px 5px 5px #7b8e8e;position:relative;z-index:1}#filter-bar .filtered-tags{display:flex;width:100%;flex-flow:row wrap;justify-content:flex-start;align-items:center}#filter-bar .filtered-tag{background-color:#effafa;color:#5ba4a4;padding-left:0.5rem;border-radius:5px;overflow:hidden;margin-right:1rem}#filter-bar .filtered-tag .close{background-color:#5ba4a4;padding:0.5rem;margin-left:0.5rem;display:inline-block;color:#fff;transition:0.3s}#filter-bar .filtered-tag .close:hover{background-color:#2c3a3a;cursor:pointer}#filter-bar button{background-color:transparent;border:none;color:#5ba4a4;font-weight:700;font-family:"Spartan",sans-serif;transition:0.3s}#filter-bar button:hover{text-decoration:underline;cursor:pointer}

